.userList {
  flex: 4;
  padding: 20px;
  height: 100vh;
  width: 100vw;
}

.userListUser {
  display: flex;
  align-items: center;
}

.userListImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.userListEdit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: #3bb077;
  color: white;
  cursor: pointer;
  margin-right: 20px;
}

.userListDelete {
  color: red;
  cursor: pointer;
}
@media only screen and (max-width: 550px) {
  .userList {
    padding: 0;
    margin-top: 50px;
  }
}
